*{
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}
body{
    background-color: rgb(68, 70, 90);
    height: 100vh;
    /* display: grid; */
    place-items: center center;
}
.Container .cards{
    z-index: 1;
    margin-top: 40px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px,1fr));
    padding: 20px;
    grid-gap: 40px;
    /* border: rgb(0, 4, 255) solid 2px; */
    justify-items: center;
    align-items: center;
}

.Container .cards .card{
    cursor: pointer;
    width: 280px;
    height: 430px;
    background-color: rgb(255, 255, 255);
    border-radius: 10px;
    box-shadow: 0 0 30px rgba(0,0,0,0,17);
    border: rgb(255, 255, 255) solid 1px;
}
.Container .cards .card .container{
    /* border: green solid 2px; */
    position: initial;
    height: 80%;
    
    /* clip-path: polygon(0 0,100% 0,100% 85%, 0 100%); */
}
.Container .cards .card .container img{
    width: 100%;
    height: 100%;
    object-fit: cover;
    /* border: rgb(238, 58, 148) solid 1px; */
    
    display: block;
    border-radius: 9px 9px 0 0;
    z-index: -5;
}



.Container .cards .details {
    height: 20%;
    padding: 20px 10px;
    text-align: center;
}

.Container .cards .details>h3 {
    font-family: "ARMTMS", Tahoma;
    height: 50%;
    color: rgb(0, 0, 0);
    font-weight: 200;
    font-size: 18px;
    /* margin: 10px 0 15px 0; */
}

.Container .cards .details>p {
    font-family: "ARMTMS", Tahoma;
    font-weight: 200;
    height: 50%;
    color: rgb(82, 82, 82);
    font-size: 15px;
    line-height: 30px;
    font-weight: 400;
}












